.sidebar {
    display: none;
  }
  
  @media (min-width: 1440px) {
    .sidebar {
      display: block;
      padding: 30px 24px;
      grid-area: sidebar;
      background-color: var(--white);
    }
  }
  
  .sidebar__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
  }
  
  .sidebar__nav {
    margin-bottom: 240px;
  }
  
  .sidebar__menu {
    gap: 30px;
  }
  
  .sidebar__profile {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
  }
  
  .sidebar__contacts {
    margin-bottom: 40px;
  }
  